.blog-Posts-Container {
  width: 100vw;
  background: white;
  /* border: 0.1rem solid black; */
  position: fixed;
  z-index: 222;
  /* top: 1%; */
  left: 0%;
  height: 100vh;
  padding: 1rem 0.2rem;
  /* border-radius: 5rem; */
  box-shadow: 0.1rem 0.1rem 50rem rgb(14, 14, 14);
}
.hide-Blog-Posts-Container {
  width: 97vw;
  background: white;
  border: 0.1rem solid black;
  position: absolute;
  top: 1%;
  right: -100%;
  height: 95vh;
  box-shadow: 0.1rem 0.1rem 50rem rgb(14, 14, 14);
}
.blog-Posts-Container .close-Blog-Btn {
  position: absolute;
  right: 2%;
  z-index: 444;
  top: 5%;
  cursor: pointer;
  width: 4rem;
  filter: invert(100%);
}

.blog-Posts-Container .post {
  width: 100%;
  height: 100%;
  padding: 1rem 0 25rem 0;
  overflow-y: auto;
  line-height: 1.9;
}
.blog-Posts-Container .post h1 {
  font-size: 3.5rem;
  letter-spacing: 0.1rem;
  padding: 2rem;
  text-transform: capitalize;
  color: #2f2e41;
}
.blog-Posts-Container .post .img-Container {
  margin: 0 auto;
  width: 60%;
}
.blog-Posts-Container .post img {
  width: 100%;
}
.blog-Posts-Container .post p {
  font-size: 1.8rem;
  letter-spacing: 0.1rem;
  padding: 1rem;
  color: #a0616a;
}

